import React from 'react';
import './MovieCard.scss';

export default function MovieCard({
  title,
  poster_path,
  vote_average,
  release_date,
  overview,
}) {
  const imageBaseUrl = 'https://image.tmdb.org/t/p/w185_and_h278_bestv2/';

  return (
    <article className="card">
      <figure className="card__figure">
        <img
          className="card__figure__img"
          src={imageBaseUrl + poster_path}
          alt={title}
        />
      </figure>
      <div className="card__main">
        <h2 className="card__main__title">{title}</h2>
        <div className="card__main__info">
          <small><span>Rating: </span>{vote_average}</small>
          <small><span>Release: </span>{release_date}</small>
        </div>
        <p className="card__main__overview">{overview}</p>
      </div>
    </article>
  );
}
